	module.exports = {"data":{"challengeNode":{"title":"Build a Survey Form","description":["<strong>目标：</strong>使用 <a href='https://codepen.io' target='_blank'>CodePen.io</a> 搭建一个与这个功能上相似的 app：<a href='https://codepen.io/freeCodeCamp/full/VPaoNP' target='_blank'>https://codepen.io/freeCodeCamp/full/VPaoNP</a>。","在满足以下<a href='https://en.wikipedia.org/wiki/User_story' target='_blank'>需求</a>并能通过所有测试的前提下，你可以根据自己的喜好来美化你的 app。","你可以使用 HTML、JavaScript 以及 CSS 来完成项目，由于目前我们只学到了 CSS，因此建议你只使用 CSS 完成这个项目，顺便还可以巩固一下之前学到的内容。你也可以使用 Bootstrap 或者是 SASS。在当前的项目中，不推荐使用其他技术如 jQurey、React、Angular 或者是 Vue，因为一旦出现问题，风险自担。但在别的项目中我们仍有机会去使用不同的技术栈比如 React，我们会接受并尽力处理你在使用建议的技术栈过程中遇到的问题，编码愉快！","<strong>需求 1：</strong>我能看见一个 H1 大小<code>id=\"title\"</code>的标题。","<strong>需求 2：</strong>我能看见一个 P 大小<code>id=\"description\"</code>的简述文字。","<strong>需求 3：</strong>我能看见一个<code>id=\"survey-form\"</code>的<code>form</code>。","<strong>需求 4：</strong>在 form 元素内，我需要在<code>id=\"name\"</code>的字段中输入我的名字。","<strong>需求 5：</strong>在 form 元素内，我需要在<code>id=\"email\"</code>的字段中输入邮箱。","<strong>需求 6：</strong>如果我输入了格式不正确的邮箱，我将会看见验证错误信息。","<strong>需求 7：</strong>在表单内，我可以在<code>id=\"number\"</code>的字段中输入数字。","<strong>需求 8：</strong>如果我在数字输入框内输入非数字，我将会看见验证错误信息。","<strong>需求 9：</strong>如果我输入的数字超出了范围（使用<code>min</code>和<code>max</code>属性定义），我将会看见验证错误信息。","<strong>需求 10：</strong>对于表单中的名称，邮箱和数字输入框应该使用对应 id 为<code>id=\"name-label\"</code>、<code>id=\"email-label\"</code>、<code>id=\"number-label\"</code>的描述标签","<strong>需求 11：</strong>在表单中的名称，邮箱和数字输入框中，我能看到各自的描述文字作为占位符。","<strong>需求 12：</strong>在表单元素内，我可以在<code>id=\"dropdown\"</code>的下拉列表中选择一个选项。","<strong>需求 13：</strong>在表单元素内，我可以从一组或多组单选按钮中选择一个字段。每组使用<code>name</code>属性进行分组。","<strong>需求 14：</strong>在表单元素内，我可以从一系列复选框中选择几个字段，每个复选框都必须具有 value 属性。","<strong>需求 15：</strong>在表单元素内，我能看见在最后有个<code>textarea</code>用于附加注释。","<strong>需求 16：</strong>在表单元素内，我能看见一个<code>id=\"submit\"</code>的按钮，用于提交我所有的输入。","你可以通过 fork 此 <a href='http://codepen.io/freeCodeCamp/pen/MJjpwO' target='_blank'>CodePen</a> 来构建项目，你也可以使用此 CDN 链接在你喜欢的任何环境中运行测试：<code>https://gitcdn.link/repo/freeCodeCamp/testable-projects-fcc/master/build/bundle.js</code>。","完成项目并通过所有测试后，输入你的项目在 CodePen 上的链接。","要是卡住的话，记得使用 <a href='https://forum.freecodecamp.one/t/topic/157' target='_blank'>Read-Search-Ask</a>。"],"challengeType":3,"fields":{"blockName":"Responsive Web Design Projects","slug":"/responsive-web-design/responsive-web-design-projects/build-a-survey-form"}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/responsive-web-design-projects/build-a-product-landing-page","id":"587d78af367417b2b2512b03"},"slug":"/responsive-web-design/responsive-web-design-projects/build-a-survey-form"}}

